@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li 
{
  @apply px-3
}
.bg-primary
{
  background: black
}
.bg-secondary
{
  background: white;
}
.mobile-nav
{
  @apply fixed top-10 left-0 bg-black w-full h-1/3;
}
.mobile-nav ul
{
  @apply w-full h-full text-center border-b-2;
}
.btn
{
  @apply bg-cyan-500 px-5 py-3 font-bold hover:border-4 text-black mx-2;
}
html
{
  scroll-behavior: smooth;
}